body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  z-index: 10;
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
  bottom: 50%;
}

.stopwatch__hand--min {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
}

.stopwatch__hand--sec {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand--min {
  animation: 600s steps(60, end) infinite rotate;
}

.stopwatch--speed-up .stopwatch__hand--sec {
  animation: 10s linear infinite rotate;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
